<template>
	<div>
		<div class="btn">
			<el-input v-model="user_id" placeholder="输入用户ID" class="search" @keyup.enter.native="search(user_id)"></el-input>
		</div>
		<div class="btn">
			<el-button type="primary" @click="search(user_id)">搜索</el-button>
		</div>
		<el-table :data="tableData">
			<el-table-column prop="id" label="用户ID" align="center"></el-table-column>
			<el-table-column prop="avatarUrl" label="头像" align="center">
				<template slot-scope="scope">
					<img :src="scope.row.avatarUrl" style="max-width:180px;max-height:80px;">
				</template>
			</el-table-column>
			<el-table-column prop="nickname" label="昵称" align="center"></el-table-column>
			<el-table-column prop="name" label="名称" align="center"></el-table-column>
			<el-table-column prop="sex" label="性别" align="center">
				<template slot-scope="scope">
					<span v-text="scope.row.sex == 1 ? '男' : '女' "></span>
				</template>
			</el-table-column>
			<el-table-column prop="phone" label="手机号码" align="center"></el-table-column>
			<el-table-column label="操作" align="center" width="300px">
				<template slot-scope="scope">
					<el-button size="mini" type="primary" @click="handleOpenPermission(scope.$index, scope.row)">开通权限</el-button>
					<el-button size="mini" type="primary" @click="handleBanUsePermission(scope.$index, scope.row)">禁止使用权限</el-button>
				</template>
			</el-table-column>
		</el-table>

		<el-dialog title="开通权限" :visible.sync="dialogOpenPermission" :close-on-click-modal="false" width="500px">
			<div class="box">
				<el-form :model="openPermissionForm">
					<div v-for="(item,index) in openPermissionForm.service" :key="index">
						<el-form-item label="选择学校">
							<el-select v-model="item.school_id" placeholder="请选择学校" @change="schoolChange">
								<el-option v-for="item in schools" :label="item.name" :value="item.id" :key="item.id"></el-option>
							</el-select>
						</el-form-item>
						<el-form-item label="选择商品">
							<el-select v-model="item.product_id" placeholder="请选择学校">
								<el-option v-for="item in goodsList" :label="item.title" :value="item.id" :key="item.id"></el-option>
							</el-select>
						</el-form-item>
					</div>
					<el-form-item label="操作">
						<el-button type="success" @click="addopenPermissionForm">添加</el-button>
						<el-button type="success" @click="delopenPermissionForm">删除</el-button>
					</el-form-item>
					<div class="submit">
						<el-form-item>
							<el-button type="primary" @click="newOpenPermission">提交</el-button>
						</el-form-item>
					</div>
				</el-form>
			</div>
		</el-dialog>

		<el-dialog title="禁止使用权限" :visible.sync="dialogBanUsePermission" :close-on-click-modal="false">
			<div class="box">
				<el-form :model="banUsePermissionForm">
					<el-form-item label="选择禁用的学校">
						<el-transfer v-model="banUsePermissionForm.school_id" :data="schoolList" :titles="['选择学校', '禁用学校']" :button-texts="['取消', '禁用']" @change="transferChange"></el-transfer>
					</el-form-item>
					<div class="submit">
						<el-form-item>
							<el-button type="primary" @click="newBanUsePermission">提交</el-button>
						</el-form-item>
					</div>
				</el-form>
				<!-- <el-tabs v-model="activeName">
					<el-tab-pane label="禁用学校" name="school">
						<el-form :model="BanUsePermissionForm">
							<el-form-item label="选择禁用的学校">
							</el-form-item>
						</el-form>
					</el-tab-pane>
					<el-tab-pane label="禁用开启权限" name="permission">
					</el-tab-pane>
				</el-tabs> -->


			</div>
		</el-dialog>

		<div class="block">
			<el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper"
			 :total="totalPage">
			</el-pagination>
		</div>
	</div>
</template>

<script>
	import API from '@/api/index.js'
	export default {
		name: 'user',
		data() {
			return {
				tableData: [],
				dialogOpenPermission: false, // 开通权限
				schools: [],
				schoolList: [],
				goodsList: [],
				openPermissionForm: {
					user_id: '',
					service: []
				},
				dialogBanUsePermission: false, // 禁止使用权限
				banUsePermissionForm: {
					user_id: '',
					type: 1,
					school_id: [],
				},
				activeName: 'school',
				user_id: '', // 搜索用户id
				currentPage: 1,
				totalPage: 0
			}
		},
		mounted() {
			this.getWxUser()
			// this.getSchool()
		},
		methods: {
			// 未禁用的学校
			getSchool() {
				var self = this;
				API.banProducts(self.banUsePermissionForm.user_id, 2).then(res => {
					self.schools = res;
					res.forEach(item => {
						self.schoolList.push({
							label: item.name,
							key: item.id
						})
					})
				})
			},
			search() {
				var self = this;
				if (self.user_id) {
					API.userSearch(self.user_id).then(res => {
						self.tableData = res.data;
						self.totalPage = res.total;
						self.user_id = '';
						self.$message.success('搜索成功！');
					})
				} else {
					self.$message.warning('请输入用户ID');
				}
			},
			schoolChange(value) {
				var self = this;
				self.getGoods(self.openPermissionForm.school)
			},
			// 获取商品列表
			getGoods(value) {
				var self = this;
				API.goods(value).then(res => {
					self.goodsList = res.data
				})
			},
			getWxUser() {
				var self = this;
				API.wxUser(self.currentPage).then(res => {
					self.tableData = res.data
					self.totalPage = res.total
				})
			},
			addopenPermissionForm() {
				var self = this;
				self.openPermissionForm.service.push({})
			},
			delopenPermissionForm() {
				var self = this;
				self.openPermissionForm.service.pop({})
			},
			handleOpenPermission(index, row) {
				var self = this;
				self.dialogOpenPermission = true;
				self.openPermissionForm = {
					user_id: '',
					service: []
				}
				self.openPermissionForm.user_id = row.id;
				console.log(self.openPermissionForm.user_id);
			},
			newOpenPermission() {
				var self = this;
				API.setProduct(self.openPermissionForm).then(res => {
					self.$message.success('提交成功'),
						self.openPermissionForm = {
							user_id: '',
							service: []
						}
					self.dialogOpenPermission = false;
				})
			},
			
			handleBanUsePermission(index, row) {
				var self = this;
				self.dialogBanUsePermission = true;
				self.banUsePermissionForm.user_id = row.id;
				self.getSchool();
				self.banUsePermissionForm.school_id = []
				API.banProducts(self.banUsePermissionForm.user_id, 1).then(res => {
					res.forEach(item => {
						self.banUsePermissionForm.school_id.push({
							label: item.name,
							key: item.id
						})
					})
					
					console.log(self.banUsePermissionForm.school_id)
				})
			},
			transferChange(val) {
				var self = this;
				self.banUsePermissionForm.school_id = [72]
				console.log(val)
				console.log(self.schoolList)
				console.log(self.banUsePermissionForm.school_id)
			},
			newBanUsePermission() {
				var self = this;
				API.banProduct(self.banUsePermissionForm).then(res => {
					self.$message.success('提交成功'),
						self.banUsePermissionForm = {
							user_id: '',
							type: 1,
							schoo_id: [],
						}
					self.dialogBanUsePermission = false;
				})

			},

			handleCurrentChange(val) {
				this.getWxUser();
			}
		}
	}
</script>

<style>
</style>
